.scroll-slider {
  position: relative;
  background-color: $color-default-white;

  z-index: 0;

  &__slides {
    position: sticky;
    left: 0;
    top: 0;
    width: 100%;

    min-height: 100vh;
    max-height: 100vh;
    /* stylelint-disable-next-line */
    min-height: calc(100 * var(--vh, 1vh));
    /* stylelint-disable-next-line */
    max-height: calc(100 * var(--vh, 1vh));
    overflow: hidden;
  }

  &__slide {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;

    min-height: 100vh;
    max-height: 100vh;
    /* stylelint-disable-next-line */
    min-height: calc(100 * var(--vh, 1vh));
    /* stylelint-disable-next-line */
    max-height: calc(100 * var(--vh, 1vh));

    opacity: 0;
    pointer-events: none;
    transform: scale(0.8) translateZ(0);

    transition: all $trans-default;

    .content-block {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      min-height: 100vh;
      /* stylelint-disable-next-line */
      min-height: calc(100 * var(--vh, 1vh));
    }

    &.is-active {
      opacity: 1;
      pointer-events: all;
      transform: scale(1) translateZ(0);
    }
  }

  &__slide-bg {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;

    min-height: 100vh;
    /* stylelint-disable-next-line */
    min-height: calc(100 * var(--vh, 1vh));

    z-index: -1;

    background-repeat: repeat;
  }
}
